/* eslint-disable no-console */
import React, { useState } from 'react';
import emailjs from 'emailjs-com';

import Input from '../components/input';
import Notification from '../components/notification';
import './contact-page.css';

const ContactsPage = () => {
  const [templateParams, setTemplateParams] = useState({});

  const handleEmailSumbit = (e) => {
    e.preventDefault();

    emailjs.send('service_gj6kb6n', 'template_9w5bbdt', templateParams, 'user_Gvy5EFerJiygU2R0klfg4').then(
      (response) => {
        console.log('SUCCESS!', response.status, response.text);
        setTemplateParams({ messageSubmitted: true });
      },
      (err) => {
        console.log('FAILED...', err);
      },
    );
  };

  const isDisabled = templateParams.messageSubmitted || !templateParams.message || !templateParams.replyEmail;

  return (
    <div>
      <form className="form" name="Contact Form">
        <h1 className="contact-header ">Contact me</h1>
        <fieldset className="fields">
          <Input
            label="Name"
            placeholder="Enter your name..."
            value={templateParams.name}
            onChange={(e) => setTemplateParams({ ...templateParams, name: e.target.value })}
          />
          <br />
          <Input
            label="Phone Number"
            placeholder="Enter your phone number..."
            value={templateParams.phoneNumber}
            onChange={(e) => setTemplateParams({ ...templateParams, phoneNumber: e.target.value })}
          />
          <br />
          <Input
            isRequired={true}
            label="Email"
            placeholder="Enter your email..."
            value={templateParams.replyEmail}
            onChange={(e) => setTemplateParams({ ...templateParams, replyEmail: e.target.value })}
          />
          <br />
          <div className="input-wrapper">
            Message<span className="required">*</span>
            <textarea
              className="message"
              placeholder="Enter a message..."
              value={templateParams.message}
              onChange={(e) => setTemplateParams({ ...templateParams, message: e.target.value })}
            />
          </div>
          <br />
          <div className="button-wrapper">
            <button
              disabled={isDisabled}
              className={`${isDisabled ? 'button-disabled' : 'primary-button'} button send`}
              type="submit"
              onClick={handleEmailSumbit}
              value="Submit"
            >
              Send
            </button>
            {isDisabled ? <span className="required">(complete required fields)</span> : null}
          </div>
        </fieldset>
      </form>
      {templateParams.messageSubmitted ? <Notification /> : null}
    </div>
  );
};

export default ContactsPage;
